import Vue from 'vue'
import App from './App.vue'
import router from './router'

// import axios from 'axios';
import VueAuthenticate from 'vue-authenticate'
import VueAxios from 'vue-axios'
import Server from './$server'

import Loading from "vue-loading-overlay";
import 'vue-loading-overlay/dist/vue-loading.css';
import TimeAgo from 'javascript-time-ago';
// Load locale-specific relative date/time formatting rules.
import en from 'javascript-time-ago/locale/en'
import {VueMasonryPlugin} from 'vue-masonry';
import InfiniteLoading from 'vue-infinite-loading';
import sitemap from './functions/sitemapMiddleware';

// custom functions
import store from "./store";
import API from "./Api";
import loader from "./loader";
import jquery from "jquery";
import currency from "./currency";
import substring from "./functions/substring";
import getDateYear from "./functions/getDateYear";
import encodedText from "./functions/encodedText";
import validate from "./functions/validate";
import updateLocalData from "./functions/updateLocalData";
import urlify from "./functions/urlify";
// import avatar from "./functions/getAvatar";

window.eventBus = new Vue();

Vue.config.productionTip = false

var top = window.screen.height - 300;
    top = top > 0 ? top/5 : 0;

var left = window.screen.width - 400;
    left = left > 0 ? left/2 : 0;

Vue.use(VueAuthenticate, {
  baseUrl: store.state.rawURI, // Your API domain
  tokenName: 'access_token',
  storageType: 'cookieStorage',
  providers: {
    github: {
      clientId: '',
      redirectUri: store.state.callbackUrl // Your client app URL
    },
    google: {
      clientId: '922921503157-3hmb5q0j26igam777c0kk403r0ghfjdr.apps.googleusercontent.com',
      redirectUri: store.state.callbackUrl, // Your client app URL
      popupOptions: { width: 552, height: 633, top: top, left: left }
    },
    facebook: {
      clientId: '',
      redirectUri: store.state.callbackUrl // Your client app URL
    },
    twitter: {
      clientId: '',
      redirectUri: store.state.callbackUrl // Your client app URL
    }
  }
});
Vue.use(VueAxios,Server);
Vue.use(Loading);
Vue.use(VueMasonryPlugin);
Vue.use(InfiniteLoading, { /* options */ });

TimeAgo.addLocale(en);
// Create relative date/time formatter.
const timeAgo = new TimeAgo('en-US');


// prototypes
Vue.prototype.$api = API;
Vue.prototype.$loader = loader;
Vue.prototype.$Jquery = jquery;
Vue.prototype.$numeral = currency;
Vue.prototype.$substring = substring;
Vue.prototype.$timeAgo = timeAgo;
Vue.prototype.$getDateYear = getDateYear;
Vue.prototype.$encodedText = encodedText;
Vue.prototype.$validate = validate;
Vue.prototype.$storeLocalData = updateLocalData;
Vue.prototype.$urlify = urlify;
Vue.prototype.$sitemap = sitemap;
// Vue.prototype.$avatar = avatar;

new Vue({
  router,
  store,
  mounted() {
    this.$sitemap();
    window.eventBus.$on("logout", id => {
      if (id === true) {
        store.commit('logout');
        store.commit('resetState');
        window.location="/";
      }
    });
  },
  render: h => h(App),
}).$mount('#app')
