// import path from 'path';
import router from '../router';

const generateSiteMap = () => {
    function getRoutesList(routes, pre)
    {
        return routes.reduce((array, route) => {
            const path = `${pre}${route.path}`;

            if (route.path !== '*') {
                array.push(path);
            }

            if (route.children) {
                array.push(...getRoutesList(route.children, `${path}/`));
            }

            return array;
        }, []);
    }

    const list = getRoutesList(router.options.routes, 'https://matchdaydeals.com')
        .map(route => `<url><loc>${route}</loc></url>`)
        .join('\r\n');
    var rou = `<urlset xmlns="http://www.sitemaps.org/schemas/sitemap/0.9" xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" xsi:schemaLocation="http://www.sitemaps.org/schemas/sitemap/0.9 http://www.sitemaps.org/schemas/sitemap/0.9/sitemap.xsd">
                ${list}
              </urlset>`;

    // console.log(rou);

};
export default generateSiteMap;
