<template>
  <div id="app" ref="wholeBody">
      <div class="tweet">
          <button id="tweetBtn">New Tweets</button>
      </div>
<!--    <Internet :data="notiData"></Internet>-->
<!--     <notification :data="notiData" />-->
    <!-- Take not that the offline tag is needed for the offline notification to work so don't remove it except you have a better one -->
<!--    <offline @detected-condition="handleConnectivityChange"></offline>-->
    <router-view />
  </div>
</template>

<script>
import ls from "./localstorage";
import Internet from "./components/Internet";
export default {
  name: "app",
  data() {
    return {
      notiData: {},
      username: "",
    };
  },
  watch: {
    $route(to) {
      var path = to;
      var userData = ls.getStorage("fansData");
      // var username;
      // if(userData.length > 0) {
      //   username = userData[0].username;
      // }else {
      //   username = this.$route.params.username;
      // }
      //
      // if (path.name === "404" && path.path === "/profile/profile/" + username) {
      //   this.$router.push("/profile/" + username);
      // }
    },
  },
  components: {Internet},
  methods: {
    async generateContents() {
      // store the user data in the system state
      if (ls.checkStorage("fansData")) {
        let fansData = ls.getStorage("fansData");
        let user_type = fansData[0].user_type;
        this.axios
          .get("/user/details")
          .then(response => {
            var userData = response.data.data;
            var data;
            if (user_type === "1") {
              data = {
                email: userData.email,
                firstname: userData.firstname,
                lastname: userData.lastname,
                username: userData.username,
                club: userData.club,
                avatar: userData.avatar,
              };
            }else {
              data = {
                email: userData.email,
                business_name: userData.business_name,
                username: userData.username,
                avatar: userData.logo,
              };
            }
            if (this.$storeLocalData(data)) {
              window.eventBus.$emit("localDataStored", true);
            }
          })
          .catch(error => {
            // console.log(error);
            return error;
          });
      }
    },
    handleConnectivityChange(status) {
      // console.log(status);
      if (status === false) {
        this.notiData = {
          type: "error",
          message:
            "Connect to an active network to enjoy the full extent of Matchdaydeals",
          isError: true,
        };
      } else {
        this.notiData = {
          type: "success",
          message: "Great!, now you can enjoy the full extent of Matchdaydeals",
          isError: true,
        };
      }
    },
    checkUserActiveTime() {
      // we check if the user is not meant to be logged in for more than 24 hours
      if (ls.checkStorage("ticket")) {
        let ticketData = ls.getStorage("ticket");
        ticketData = ticketData[0].token_generation;
        let tsYesterday = ticketData - (24 * 3600);

        // console.log(ticketData)

      }
    },
    getUserContributions() {
      if (ls.checkStorage("fansData")) {
        let userData = localStorage.fansData;
        userData = JSON.parse(userData);
        let id = userData[0].id;
        this.axios.get("/user/contributions/" + id)
                .then(response => {
                  const payload = {
                    followers: response.data.data.followers,
                    followings: response.data.data.followings,
                    reviews: response.data.data.reviews,
                  };
                  this.$store.commit("setContributions",payload)
                })
                .catch(error => {
                  // console.log(error);
                  return error;
                })
      }
    },
  },
  async mounted() {
    await this.generateContents();
    window.eventBus.$on("loadUserData", val => {
      if (val === true) {
        this.generateContents();
      }
    });
    setInterval(()=>{
      this.getUserContributions();
    },50000);
    this.checkUserActiveTime();
    this.getUserContributions();
    // console.log(process.env.NODE_ENV)

      // load feeds on click of a button
      $('#tweetBtn').click(() => {
          $('html,body').animate({
              scrollTop: 0,
          }, 1200);
          window.eventBus.$emit('loadFeeds', true);
          setTimeout(function () {
              $('.tweet').hide()
          },1000);
          return false;
      })

      // check if scroll is at the top
      $("body").scroll(function()
      {
          var div = $(this);
          if (div[0].scrollHeight - div.scrollTop() == div.height())
          {
              alert("Reached the bottom!");
          }
          else if(div.scrollTop() == 0)
          {
              alert("Reached the top!");
          }
      });
      // show tweet button if new review is requested
      window.eventBus.$on('newReview', val => {
          if (val === true) {
              $('.tweet').show()
          }
      })

  },
};
</script>

